import React, { useEffect, useLayoutEffect, useState } from "react";
import {
  Box,
  Button,
  Container,
  Snackbar,
  Checkbox,
  Typography,
  TextField,
  CircularProgress,
} from "@mui/material";
import { makeStyles } from "@mui/styles";
import VisibilityIcon from "@mui/icons-material/Visibility";
import VisibilityOffIcon from "@mui/icons-material/VisibilityOff";
import AuthContainer from "../../../containers/authContainer";
import { NavLink, useHistory } from "react-router-dom";
import { useFormik } from "formik";
import { useDispatch, useSelector } from "react-redux";
import {
  postRegisterData,
  setAnswerRegister,
} from "../../../redux/reducers/register";
import { Alert } from "@mui/material";
import FormControlLabel from "@mui/material/FormControlLabel";
import cookie from "cookie_js";

const Register = ({ t }) => {
  const dispatch = useDispatch();
  const history = useHistory();
  const [eyes, setEyes] = useState({ password: true, password2: true });
  const toggleEyes = (key, value) => {
    setEyes((eyes) => ({ ...eyes, [key]: !eyes[value] }));
  };
  const [reload, setReload] = useState(false);
  const { answerRegisterAlert } = useSelector(
    ({ registerReducer }) => registerReducer
  );

  const formik = useFormik({
    initialValues: {
      firstname: "",
      lastname: "",
      email: "",
      password: "",
      password_confirm: "",
    },
    terms: false,
    onSubmit: (values) => {
      dispatch(postRegisterData(values));
      cookie.set("ActivationAccountEmail", values.email, { path: "/" });
      setReload(true);
    },
  });

  const firstUpdate = React.useRef(true);
  useLayoutEffect(() => {
    if (firstUpdate.current) {
      firstUpdate.current = false;
      return;
    }
    if (answerRegisterAlert.severity === 200) {
      setTimeout(() => {
        history.push("/accounts/activation");
        dispatch(setAnswerRegister({ ...alert, open: false }));
      }, 1000);
    }
    setReload(false);
    // eslint-disable-next-line
  }, [answerRegisterAlert.severity]);

  const useStyles = makeStyles((theme) => ({
    formControlPassword: {
      "& .MuiOutlinedInput-root": {
        background: "transparent!important",
        color: "#fff",
      },

      "& fieldset": {
        boxShadow: "none!important",
      },

      "& .MuiOutlinedInput-input": {
        background: "transparent !important",
        color: "#fff",
        boxShadow: "none!important",
        paddingRight: "50px",
      },

      "& .MuiInputLabel-formControl": {
        color: "#B8C0CC !important",
        fontWeight: 300,
        whiteSpace: "nowrap",
        textOverflow: "ellipsis",
        overflow: "hidden",
        maxWidth: "90%",
        fontSize: 16,
      },
      "& .MuiOutlinedInput-notchedOutline": {
        borderColor: "#B8C0CC",
      },
    },
    formControl: {
      "& .MuiOutlinedInput-root": {
        background: "transparent!important",
        color: "#fff",
      },

      "& fieldset": {
        boxShadow: "none!important",
      },

      "& .MuiOutlinedInput-input": {
        background: "transparent",
        boxShadow: "none!important",
        color: "#fff",
      },
      "& .MuiInputLabel-formControl": {
        color: "#B8C0CC !important",
        fontWeight: 300,
        whiteSpace: "nowrap",
        textOverflow: "ellipsis",
        overflow: "hidden",
        maxWidth: "90%",
        fontSize: 16,
      },
      "& .MuiOutlinedInput-notchedOutline": {
        borderColor: "#B8C0CC",
      },
    },
  }));
  const classes = useStyles();

  useEffect(() => {
    document.title = t("CreateAccount");
  }, []);

  return (
    <section className="dark">
      <Snackbar
        open={answerRegisterAlert.open}
        autoHideDuration={2000}
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
        onClose={() => dispatch(setAnswerRegister({ ...alert, open: false }))}
      >
        <Alert
          onClose={() => dispatch(setAnswerRegister({ ...alert, open: false }))}
          severity={answerRegisterAlert.severity === 200 ? "success" : "error"}
          variant="filled"
        >
          {answerRegisterAlert.severity === 200
            ? t("successfullyLoginUpMessage")
            : answerRegisterAlert.message &&
              answerRegisterAlert.message.messages}
        </Alert>
      </Snackbar>
      <Container maxWidth="lg">
        <AuthContainer user>
          <Typography variant="h3" gutterBottom component="div">
            {t("CreateAccount")}
          </Typography>
          <Typography
            style={{ fontSize: 20 }}
            variant="subtitle1"
            gutterBottom
            component="div"
          >
            {t("HaveAnAccount")}{" "}
            <NavLink to="/accounts/login" className="link">
              {t("LogIn")}
            </NavLink>
          </Typography>
          <form onSubmit={formik.handleSubmit} autoComplete="off">
            <Box display="flex" justifyContent="space-between">
              <Box style={{ width: "48%" }}>
                <span className="label">{t("Name")}</span>
                <TextField
                  fullWidth
                  label={`${t("LabelName")}`}
                  className={classes.formControl}
                  type="text"
                  variant="outlined"
                  name="firstname"
                  value={formik.values.firstname}
                  onChange={formik.handleChange}
                  inputProps={{ pattern: "[A-Za-zА-Яа-яЁё-s]{1,22}" }}
                  required
                />
              </Box>
              <Box style={{ width: "48%" }}>
                <span className="label">{t("LastName")}</span>
                <TextField
                  fullWidth
                  label={t("LabelLastName")}
                  type="text"
                  variant="outlined"
                  className={classes.formControl}
                  name="lastname"
                  value={formik.values.lastname}
                  onChange={formik.handleChange}
                  inputProps={{ pattern: "[A-Za-zА-Яа-яЁё-s]{1,22}" }}
                  required
                />
              </Box>
            </Box>
            <span className="label">E-mail</span>
            <TextField
              fullWidth
              label={t("LabelEmail")}
              type="email"
              variant="outlined"
              name="email"
              className={classes.formControl}
              value={formik.values.email}
              onChange={formik.handleChange}
              required
            />
            <span className="label">{t("Password")}</span>
            <Box style={{ position: "relative", marginBottom: 8 }}>
              <TextField
                fullWidth
                type={eyes.password ? "password" : "text"}
                id="outlined-basic"
                label={t("LabelPassword")}
                variant="outlined"
                name="password"
                className={classes.formControlPassword}
                value={formik.values.password}
                onChange={formik.handleChange}
                required
              />
              <span
                onClick={() => toggleEyes("password", "password")}
                className="form__eyes"
              >
                {eyes.password ? <VisibilityOffIcon /> : <VisibilityIcon />}
              </span>
            </Box>

            <Box style={{ position: "relative" }}>
              <TextField
                fullWidth
                type={eyes.password2 ? "password" : "text"}
                id="outlined-basic"
                label={t("LabelRepeatNewPassword")}
                variant="outlined"
                className={classes.formControlPassword}
                name="password_confirm"
                value={formik.values.password_confirm}
                onChange={formik.handleChange}
                required
              />
              <span
                onClick={() => toggleEyes("password2", "password2")}
                className="form__eyes"
              >
                {eyes.password2 ? <VisibilityOffIcon /> : <VisibilityIcon />}
              </span>
            </Box>
            <Box display="flex" alignItems="center" style={{ marginTop: 10 }}>
              <FormControlLabel
                style={{ marginRight: 5 }}
                control={<Checkbox defaultChecked color="primary" />}
                label=""
                name="terms"
                onChange={() =>
                  formik.setFieldValue("terms", !formik.values.terms)
                }
                value={formik.values.terms}
              />
              <Typography
                style={{ fontSize: 14, textAlign: "start", fontWeight: "200" }}
              >
                {t("TermsOfUseButton")}
                <NavLink
                  target="_blank"
                  to="/main/terms-of-use"
                  style={{ color: "#FCE34D", textDecoration: "underline" }}
                >
                  {t("TermsOfUse")}
                </NavLink>
              </Typography>
            </Box>
            <Button
              style={{ marginTop: 24, height: 56, width: "100%" }}
              color="primary"
              variant="contained"
              size="large"
              disabled={formik.values.terms}
              type="submit"
            >
              {reload ? (
                <CircularProgress color="secondary" disableShrink />
              ) : (
                t("Registration")
              )}
            </Button>
          </form>
        </AuthContainer>
      </Container>
    </section>
  );
};

export default Register;
